		props: {
			voicePath: { //默认地址
				type: String,
				default: '' 
			},
			maxTime: { // 录音最大时长，单位秒
				type: Number,
				default: 15
			},
			minTime: { // 录音最大时长，单位毫秒
				type:Number ,
				default: 5
			},
		},		
		data() {
			return {				
